var isNode = require("is-node");

var faker = require("faker/locale/en");

var key;
var ANIMALS = ["dog", "cat", "bird", "barnyard", "reptile", "smallfurry", "horse", "pig"];

var time = function time() {
  return 1000 + Math.floor(Math.random() * 3000);
};

var imageAPI = ["http://placecorgi.com", "http://placekitten.com", "http://placebear.com"];

var imageGen = function imageGen() {
  return "".concat(imageAPI[Math.floor(Math.random() * imageAPI.length)], "/").concat(498 + Math.floor(Math.random() * 4), "/").concat(498 + Math.floor(Math.random() * 4));
};

var promiseGen = function promiseGen(output) {
  return new Promise(function (resolve) {
    return setTimeout(function () {
      return resolve(output);
    }, time());
  });
};

var petGen = function petGen() {
  return {
    contact: {
      state: faker.address.stateAbbr(),
      city: faker.address.city()
    },
    media: {
      photos: {
        photo: [{
          "@size": "pn",
          "@id": "1",
          value: imageGen()
        }, {
          "@size": "pn",
          "@id": "2",
          value: imageGen()
        }, {
          "@size": "pn",
          "@id": "3",
          value: imageGen()
        }, {
          "@size": "pn",
          "@id": "4",
          value: imageGen()
        }, {
          "@size": "pn",
          "@id": "5",
          value: imageGen()
        }]
      }
    },
    id: faker.random.number(10000),
    shelterPetId: null,
    breeds: {
      breed: faker.lorem.word()
    },
    name: faker.name.firstName(),
    description: faker.lorem.paragraph(10),
    animal: faker.lorem.word()
  };
};

var api = {
  breed: {
    list: function list(opts) {
      return promiseGen({
        petfinder: {
          breeds: {
            breed: Array.from({
              length: 5
            }).map(function () {
              return faker.lorem.word();
            })
          }
        }
      });
    }
  },
  pet: {
    get: function get(opts) {
      return promiseGen({
        petfinder: {
          pet: petGen()
        }
      });
    },
    find: function find(opts) {
      return promiseGen({
        petfinder: {
          pets: {
            pet: Array.from({
              length: 10
            }).map(function () {
              return petGen();
            })
          }
        }
      });
    }
  },
  shelter: {
    getPets: function getPets(opts) {
      return request("shelter.getPets", opts);
    },
    listByBreed: function listByBreed(opts) {
      return request("shelter.listByBreed", opts);
    },
    find: function find(opts) {
      return request("shelter.find", opts);
    },
    get: function get(opts) {
      return request("shelter.get", opts);
    }
  }
};

module.exports = function createPetfinderSingleton(creds) {
  if (creds) {
    key = creds.key;
  }

  return api;
};

module.exports.ANIMALS = ANIMALS;